import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    children: [{
      path: '/',
      name: 'website',
      component: () => import(/* webpackChunkName: "website" */ '../views/home/websit.vue')
    },{
      path: '/vis',
      name: 'vis',
      component: () => import(/* webpackChunkName: "vis" */ '../views/home/vis.vue')
    },{
      path: '/ins',
      name: 'ins',
      component: () => import(/* webpackChunkName: "ins" */ '../views/home/ins.vue')
    }]
  },
  {
    path: '/c',
    name: 'Caregiver',
    component: () => import(/* webpackChunkName: "caregiver" */ '../views/caregiver.vue')
  },
  // {
  //   // path: '/about',
  //   // name: 'About',
  //   // // route level code-splitting
  //   // // this generates a separate chunk (about.[hash].js) for this route
  //   // // which is lazy-loaded when the route is visited.
  //   // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

export default router
