<template>
  <div id="home">
    <topnav @showlog="showlog" :degree='degree'></topnav>
    <div v-if="!logshow" class="mongolia" @click="showlog"></div>
    <log-box v-show="!logshow"></log-box>
    <div class="router">
      <keep-alive include="website">
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import topnav from "@/components/topNav.vue";
import logbox from "@/components/login.vue";
import menubox from "@/components/menu.vue";

export default {
  data() {
    return {
      logshow: true,
      menushow: false,
      degree: 0,
    };
  },
  methods: {
    // show login box
    showlog() {
      this.logshow = !this.logshow;
      this.menushow = false;
    },
  },
  components: {
    topnav: topnav,
    "log-box": logbox,
    "menu-box": menubox,
  },
};
</script>
<style scoped>
#home {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.mongolia {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.router {
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  top: 60px;
  /* z-index: -1; */
}
</style>